.profile__settings-field{
    width: 258px;
    height: 33px;
    background: #FFFFFF;
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    border-radius: 4px;
    padding-left: 10px;
    padding-right: 10px;
    margin-bottom: 10px;
}

.profile__settings-field-name{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 13px;
    color: #041D22;
    margin-bottom: 5px;
}

.directory-settings-field{
    background: url("./img/directory-settings-field-arrow.svg") no-repeat;
    background-position: 95% 50%;
}

.profile__to-plug-social-link-button{
    display: block;
    width: 40%;
    text-align: right;
    margin-left: auto;
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    padding-top: 4px;
    color: #546164;
}