.fc .fc-button-primary{
    background: #5EC8D9;
}

.fc-theme-standard td, .fc-theme-standard th {
    border: 1px solid #5EC8D9;
}

.fc .fc-toolbar.fc-header-toolbar {
    padding: 0.75em;
    border: 1px solid #5EC8D9;
    margin: 0;
}

.fc .fc-toolbar-title{
    font-family: "Gotham Pro";
    color: #5EC8D9;
}

.fc .fc-view-harness{
    border-left: 1px solid #5EC8D9;
    border-right: 1px solid #5EC8D9;
    border-bottom: 1px solid #5EC8D9;
}

.fc-daygrid-day{
    cursor: pointer;
}