.account__profile{
    width: 77.760416666666%;
    padding-top:38px;
    padding-left:40px;
    background-color: #F3F4F6;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
}

.account__profile-headline{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    color: #4F4F4F;
    margin-bottom: 61px;
    width: 100%;
    height: 24px;
}

.profile__subscriptions-and-cards{
    width: 45.3%;
}

@media all and (max-width: 1680px){
    .profile__subscriptions-and-cards{
        width: 36%;
    }
}

@media all and (max-width: 1366px){
    .account__profile{
        padding-left:20px;
        justify-content: center;
    }

    .profile__subscriptions-and-cards{
        width: 86%;
        margin-bottom: 30px;
    }
}