.profile__user-contacts{
    background: #ffffff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    width: 44.1%;
    padding: 30px;
    position: relative;
    margin-right: 30px;
}

.profile___user-contacts-headline{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    color: #000000;
    margin-bottom: 24px;
}

.profile__edit-profile-button{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    color: #5EC8D9;
    position: absolute;
    right: 30px;
    top: 36px;
}

.profile__user-contacts-body{
    display: flex;
    justify-content: space-between;
}

.profile__user-attributes{
    width: 35.78125%;
}

.profile__contacts-attributes{
    width: 43.90625%;
}

.profile__attribute{
    width: 100%;
    border-bottom: 1px solid #E6E7E7;
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    padding-top:14px;
    padding-bottom:14px;
    font-family: Gotham Pro;
    font-style: normal;
    font-size: 14px;
    color: #041D22;
}

.profile__attribute-name{
    font-weight: 800;
}

.profile__attribute-value{
    font-weight: 500;
}

@media all and (max-width: 1680px){
    .profile__user-contacts{
        width: 53%;
    }

    .profile__user-attributes{
        width: 45%;
    }
}

@media all and (max-width: 1366px){
    .profile__user-contacts{
        width: 86%;
        margin-right: 0;
        margin-bottom: 30px;
    }
}