.profile__tariff-block{
    background: #5EC8D9;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    padding-top: 25px;
    padding-left: 30px;
    display: block;
    position: relative;
    width: 36%;
    max-height: 175px;
}

.tariff-block__indicator-name{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 13px;
    color: #FFFFFF;
    margin-bottom: 14px;
}

.tariff-block__indicator-value{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 24px;
    line-height: 23px;
    color: #FFFFFF;
    margin-bottom: 24px;
}

.change-tariff{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    color: #FFFFFF;
    position: absolute;
    top:78%;
    right: 7%;
}

@media all and (max-width: 1680px){
    .tariff-block__indicator-value{
        font-size: 18px;
    }
}

@media all and (max-width: 1366px){
    .profile__tariff-block{
        width: 86%;
        margin-bottom: 30px;
    }
}