.profile__contacts-settings-block{
    width: 85.4%;
    background-color: #ffffff;
    max-height: 544px;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    padding: 30px 25px;
}

.profile__contacts-settings-headline{
    margin-bottom: 20px;
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    color: #041D22;
    width: 100%;
}

.contacts-settings__unique-attributes{
    display: flex;
    flex-wrap: wrap;
    /*width: 26%;*/
    width: 307px;
    flex-grow:0;
}

.contacts-settings__unique-attributes .avatar-section{
    position: static;
    margin-top: 0;
    max-width: 188px;
    margin-right: 0;
    height: 100px;
}

.contacts-settings__unique-attributes .avatar{
    margin: 0 auto;
    margin-bottom: 22px;
    width: 85%;
}

.contacts-settings__unique-attributes .change-avatar{
    display: block;
    color: #535353;
}

.contacts-setting__standard-attributes{
    width: 72.785829307568%;
}

.contacts-settings-block___manage-buttons{
    width: 100%;
    display: flex;
    justify-content: center;
    padding-top: 38px;
    padding-bottom: 5px;
}

.contacts-settings-block___manage-buttons .button{
    width: 224px;
    height: 35px;
}

.contacts-settings-block___manage-buttons .button:first-child{
    margin-right: 29px;
}