.birth-date-section__headline{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    color: #041D22;
    margin-bottom: 5px;
}

.birth-date{
    display: flex;
}

.birth-date__field{
    background: #FFFFFF;
    border: 1px solid #C4C4C4;
    box-sizing: border-box;
    border-radius: 4px;
    height: 33px;
    margin-right: 10px;
    cursor: pointer;
    padding: 2px 10px 2px 10px;
    position: relative;
    display: flex;
    align-items: center;
}

.birth-date__field::after{
    content: url("./img/fields-arrow.svg");
    position: absolute;
    height: 100%;
    left: 79%;
}

.birth-date__field-content{
    height: auto;
    font-size: 14px;
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    color: #041D22;
}

.birth-date__day{
    width: 59px;
}

.birth-date__month{
    width: 112px;
}

.birth-date__year{
    width: 67px;
}