.profile__basic-information{
    width: 53.4%;
    background: #FFFFFF;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    position: relative;
    height: 175px;
    padding-left: 17px;
    padding-right: 30px;
    display: flex;
    margin-right: 30px;
    margin-bottom: 30px;
}

.basic-information__content{
    width: 70.91377091377%;
    margin-left: auto;
}

.basic-information__about-user{
    padding-top: 27px;
    padding-bottom: 18px;
    border-bottom: 1px solid #5EC8D9;
    display:flex;
}

.basic-information__about-subscriptions{
    padding-top: 18px;
    padding-bottom: 23px;
    display: flex;
    justify-content: space-between;
}

.online-status{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 11px;
    line-height: 11px;
    color: #5EC8D9;
    margin-left: auto;
    margin-bottom: 5px;
    text-align: right;
}

.basic-information__name{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    color: #4F4F4F;
}

.basic-information__name-section{
    margin-right: 54px;
}

.basic-information__location-section{
    padding-top:16px;
    display: flex;
}

.map-marker{
    margin-right:3px;
}

.recurrent-expenses-section__headline , .expenses-sum, .chat-bots-quantity__headline , .chat-bots-quantity, .subscription-quantity__headline, .subscription-quantity, .community-quantity__headline, .community-quantity{
    text-align: center;
}

.about-subscriptions-headline{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 11px;
    color: #2B555D;
    margin-bottom: 15px;
}

.about-subscriptions-value{
    font-family: Gotham Pro;
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 19px;
    color: #5EC8D9;
}


@media all and (max-width: 1680px){
    .basic-information__content{
        width: 65%;
    }
}

@media all and (max-width: 1366px){
    .basic-information__content{
        width: 65%;
    }

    .profile__basic-information{
        width: 86%;
        margin-right: 0;
        margin-bottom: 30px;
    }

}

